          color: $color-dark\n          &:hover,\n          &.is-hovered\n            background-color: bulmaDarken($color-light, 2.5%)\n            border-color: transparent\n            color: $color-dark\n          &:active,\n          &.is-active\n            background-color: bulmaDarken($color-light, 5%)\n            border-color: transparent\n            color: $color-dark\n  // Sizes\n  &.is-small\n    +button-small\n  &.is-normal\n    +button-normal\n  &.is-medium\n    +button-medium\n  &.is-large\n    +button-large\n  // Modifiers\n  &[disabled],\n  fieldset[disabled] &\n    background-color: $button-disabled-background-color\n    border-color: $button-disabled-border-color\n    box-shadow: $button-disabled-shadow\n    opacity: $button-disabled-opacity\n  &.is-fullwidth\n    display: flex\n    width: 100%\n  &.is-loading\n    color: transparent !important\n    pointer-events: none\n    &::after\n      @extend %loader\n      +center(1em)\n      position: absolute !important\n  &.is-static\n    background-color: $button-static-background-color\n    border-color: $button-static-border-color\n    color: $button-static-color\n    box-shadow: none\n    pointer-events: none\n  &.is-rounded\n    border-radius: $radius-rounded\n    padding-left: calc(#{$button-padding-horizontal} + 0.25em)\n    padding-right: calc(#{$button-padding-horizontal} + 0.25em)\n\n.buttons\n  align-items: center\n  display: flex\n  flex-wrap: wrap\n  justify-content: flex-start\n  .button\n    margin-bottom: 0.5rem\n    &:not(:last-child):not(.is-fullwidth)\n      +ltr-property(\"margin\", 0.5rem)\n  &:last-child\n    margin-bottom: -0.5rem\n  &:not(:last-child)\n    margin-bottom: 1rem\n  // Sizes\n  &.are-small\n    .button:not(.is-normal):not(.is-medium):not(.is-large)\n      +button-small\n  &.are-medium\n    .button:not(.is-small):not(.is-normal):not(.is-large)\n      +button-medium\n  &.are-large\n    .button:not(.is-small):not(.is-normal):not(.is-medium)\n      +button-large\n  &.has-addons\n    .button\n      &:not(:first-child)\n        border-bottom-left-radius: 0\n        border-top-left-radius: 0\n      &:not(:last-child)\n        border-bottom-right-radius: 0\n        border-top-right-radius: 0\n        +ltr-property(\"margin\", -1px)\n      &:last-child\n        +ltr-property(\"margin\", 0)\n      &:hover,\n      &.is-hovered\n        z-index: 2\n      &:focus,\n      &.is-focused,\n      &:active,\n      &.is-active,\n      &.is-selected\n        z-index: 3\n        &:hover\n          z-index: 4\n      &.is-expanded\n        flex-grow: 1\n        flex-shrink: 1\n  &.is-centered\n    justify-content: center\n    &:not(.has-addons)\n      .button:not(.is-fullwidth)\n        margin-left: 0.25rem\n        margin-right: 0.25rem\n  &.is-right\n    justify-content: flex-end\n    &:not(.has-addons)\n      .button:not(.is-fullwidth)\n        margin-left: 0.25rem\n        margin-right: 0.25rem\n\n@each $bp-name, $bp-sizes in $button-responsive-sizes\n  +breakpoint($bp-name)\n    @each $size, $value in $bp-sizes\n      @if $size != \"normal\"\n        .button.is-responsive.is-#{$size}\n          font-size: $value\n      @else\n        .button.is-responsive,\n        .button.is-responsive.is-normal\n          font-size: $value\n","@import \"../utilities/mixins\"\n\n$container-offset: (2 * $gap) !default\n$container-max-width: $fullhd !default\n\n.container\n  flex-grow: 1\n  margin: 0 auto\n  position: relative\n  width: auto\n  &.is-fluid\n    max-width: none !important\n    padding-left: $gap\n    padding-right: $gap\n    width: 100%\n  +desktop\n    max-width: $desktop - $container-offset\n  +until-widescreen\n    &.is-widescreen:not(.is-max-desktop)\n      max-width: min($widescreen, $container-max-width) - $container-offset\n  +until-fullhd\n    &.is-fullhd:not(.is-max-desktop):not(.is-max-widescreen)\n      max-width: min($fullhd, $container-max-width) - $container-offset\n  +widescreen\n    &:not(.is-max-desktop)\n      max-width: min($widescreen, $container-max-width) - $container-offset\n  +fullhd\n    &:not(.is-max-desktop):not(.is-max-widescreen)\n      max-width: min($fullhd, $container-max-width) - $container-offset\n","@import \"../utilities/mixins\"\n\n$content-heading-color: $text-strong !default\n$content-heading-weight: $weight-semibold !default\n$content-heading-line-height: 1.125 !default\n\n$content-block-margin-bottom: 1em !default\n\n$content-blockquote-background-color: $background !default\n$content-blockquote-border-left: 5px solid $border !default\n$content-blockquote-padding: 1.25em 1.5em !default\n\n$content-pre-padding: 1.25em 1.5em !default\n\n$content-table-cell-border: 1px solid $border !default\n$content-table-cell-border-width: 0 0 1px !default\n$content-table-cell-padding: 0.5em 0.75em !default\n$content-table-cell-heading-color: $text-strong !default\n$content-table-head-cell-border-width: 0 0 2px !default\n$content-table-head-cell-color: $text-strong !default\n$content-table-body-last-row-cell-border-bottom-width: 0 !default\n$content-table-foot-cell-border-width: 2px 0 0 !default\n$content-table-foot-cell-color: $text-strong !default\n\n.content\n  @extend %block\n  // Inline\n  li + li\n    margin-top: 0.25em\n  // Block\n  p,\n  dl,\n  ol,\n  ul,\n  blockquote,\n  pre,\n  table\n    &:not(:last-child)\n      margin-bottom: $content-block-margin-bottom\n  h1,\n  h2,\n  h3,\n  h4,\n  h5,\n  h6\n    color: $content-heading-color\n    font-weight: $content-heading-weight\n    line-height: $content-heading-line-height\n  h1\n    font-size: 2em\n    margin-bottom: 0.5em\n    &:not(:first-child)\n      margin-top: 1em\n  h2\n    font-size: 1.75em\n    margin-bottom: 0.5714em\n    &:not(:first-child)\n      margin-top: 1.1428em\n  h3\n    font-size: 1.5em\n    margin-bottom: 0.6666em\n    &:not(:first-child)\n      margin-top: 1.3333em\n  h4\n    font-size: 1.25em\n    margin-bottom: 0.8em\n  h5\n    font-size: 1.125em\n    margin-bottom: 0.8888em\n  h6\n    font-size: 1em\n    margin-bottom: 1em\n  blockquote\n    background-color: $content-blockquote-background-color\n    +ltr-property(\"border\", $content-blockquote-border-left, false)\n    padding: $content-blockquote-padding\n  ol\n    list-style-position: outside\n    +ltr-property(\"margin\", 2em, false)\n    margin-top: 1em\n    &:not([type])\n      list-style-type: decimal\n      &.is-lower-alpha\n        list-style-type: lower-alpha\n      &.is-lower-roman\n        list-style-type: lower-roman\n      &.is-upper-alpha\n        list-style-type: upper-alpha\n      &.is-upper-roman\n        list-style-type: upper-roman\n  ul\n    list-style: disc outside\n    +ltr-property(\"margin\", 2em, false)\n    margin-top: 1em\n    ul\n      list-style-type: circle\n      margin-top: 0.5em\n      ul\n        list-style-type: square\n  dd\n    +ltr-property(\"margin\", 2em, false)\n  figure\n    margin-left: 2em\n    margin-right: 2em\n    text-align: center\n    &:not(:first-child)\n      margin-top: 2em\n    &:not(:last-child)\n      margin-bottom: 2em\n    img\n      display: inline-block\n    figcaption\n      font-style: italic\n  pre\n    +overflow-touch\n    overflow-x: auto\n    padding: $content-pre-padding\n    white-space: pre\n    word-wrap: normal\n  sup,\n  sub\n    font-size: 75%\n  table\n    width: 100%\n    td,\n    th\n      border: $content-table-cell-border\n      border-width: $content-table-cell-border-width\n      padding: $content-table-cell-padding\n      vertical-align: top\n    th\n      color: $content-table-cell-heading-color\n      &:not([align])\n        text-align: inherit\n    thead\n      td,\n      th\n        border-width: $content-table-head-cell-border-width\n        color: $content-table-head-cell-color\n    tfoot\n      td,\n      th\n        border-width: $content-table-foot-cell-border-width\n        color: $content-table-foot-cell-color\n    tbody\n      tr\n        &:last-child\n          td,\n          th\n            border-bottom-width: $content-table-body-last-row-cell-border-bottom-width\n  .tabs\n    li + li\n      margin-top: 0\n  // Sizes\n  &.is-small\n    font-size: $size-small\n  &.is-normal\n    font-size: $size-normal\n  &.is-medium\n    font-size: $size-medium\n  &.is-large\n    font-size: $size-large\n","$icon-dimensions: 1.5rem !default\n$icon-dimensions-small: 1rem !default\n$icon-dimensions-medium: 2rem !default\n$icon-dimensions-large: 3rem !default\n$icon-text-spacing: 0.25em !default\n\n.icon\n  align-items: center\n  display: inline-flex\n  justify-content: center\n  height: $icon-dimensions\n  width: $icon-dimensions\n  // Sizes\n  &.is-small\n    height: $icon-dimensions-small\n    width: $icon-dimensions-small\n  &.is-medium\n    height: $icon-dimensions-medium\n    width: $icon-dimensions-medium\n  &.is-large\n    height: $icon-dimensions-large\n    width: $icon-dimensions-large\n\n.icon-text\n  align-items: flex-start\n  color: inherit\n  display: inline-flex\n  flex-wrap: wrap\n  line-height: $icon-dimensions\n  vertical-align: top\n  .icon\n    flex-grow: 0\n    flex-shrink: 0\n    &:not(:last-child)\n      +ltr\n        margin-right: $icon-text-spacing\n      +rtl\n        margin-left: $icon-text-spacing\n    &:not(:first-child)\n      +ltr\n        margin-left: $icon-text-spacing\n      +rtl\n        margin-right: $icon-text-spacing\n\ndiv.icon-text\n  display: flex\n","@import \"../utilities/mixins\"\n\n$dimensions: 16 24 32 48 64 96 128 !default\n\n.image\n  display: block\n  position: relative\n  img\n    display: block\n    height: auto\n    width: 100%\n    &.is-rounded\n      border-radius: $radius-rounded\n  &.is-fullwidth\n    width: 100%\n  // Ratio\n  &.is-square,\n  &.is-1by1,\n  &.is-5by4,\n  &.is-4by3,\n  &.is-3by2,\n  &.is-5by3,\n  &.is-16by9,\n  &.is-2by1,\n  &.is-3by1,\n  &.is-4by5,\n  &.is-3by4,\n  &.is-2by3,\n  &.is-3by5,\n  &.is-9by16,\n  &.is-1by2,\n  &.is-1by3\n    img,\n    .has-ratio\n      @extend %overlay\n      height: 100%\n      width: 100%\n  &.is-s